import './IndexCarrito.scss'
import React, { Fragment } from 'react'
import { useSelector } from 'react-redux'
import { BanderaCategoria } from '../productos/BanderaCategoria'
import { useDispatch } from 'react-redux'
import { remove_product_action} from '../../redux/actions/action'
import cross from '../../images/cross.svg'
import { AcumuladorCarrito } from './AcumuladorCarrito'
import { Link } from 'react-router-dom'
import { ButtonAgregar } from '../ButtonAgregar'
import { SweetAlert } from '../../util/SetupAlert'

const IndexCarrito = () => {
    const productos = useSelector((state)=> state.reducer_product.product)
    
    const acumuladorPrecios = () =>{
        let result = 0;
        productos.map(prod => result += (prod.precioFinal * prod.cantidad))
        return result
    }
    const gratitude = ()=>{
        SweetAlert("success","Muchas gracias por su compra")
    }
    return (
        <Fragment>
            <BanderaCategoria categoria="Carrito"/>
            <section className="carrito-productos" style={{minHeight: "70vh",}}>
                {(productos.length > 0) && productos.map(el => <ItemCarrito key={el.id}  elemento={el}/>)}
                {(productos.length > 0)? 
                <>
                    <article className="item-total" >
                        <div style={{width: "80%"}}>TOTAL</div>
                        <div style={{width: "20%"}}>$ {acumuladorPrecios()}</div>
                    </article>
                    <article className="item-total" style={{marginTop: "10px"}}>
                        <div style={{width: "80%"}}></div>
                        <div style={{width: "20%"}}><ButtonAgregar onClick={gratitude} style={{padding: "10px",width: "90%"}} >Pagar</ButtonAgregar></div>
                    </article>
                </>
                 :
                <div style={{textAlign: "center"}}>No hay items en el carrito. Agrega productos para visualizarlos en esta seccion.</div> 
                }
            </section>
        </Fragment>
    )
}

export const ItemCarrito = ({elemento}) => {
    const dispatch = useDispatch()
    const precioTotal = elemento.precioFinal * elemento.cantidad;
    return (
        <article className="item-carrito">
            <div className="producto-img">
                <img src={elemento.imagen} alt={elemento.nombre}/>
            </div>
            <div className="producto-descripcion">
                <span className='nombre-producto'>
                    <Link to={`productos/${elemento.id}`} style={{color: "333 !important"}}>
                        {elemento.nombre}
                    </Link>&nbsp;&nbsp;
                    {(elemento.descuento)&& <span className='cartel-descuento'>Descuento del {elemento.descuento} %</span>}
                </span>
                
                <AcumuladorCarrito elemento={elemento} />
            </div>
            <div className="boton-eliminar">
                <img onClick={() => dispatch(remove_product_action(elemento.id))} src= {cross} style={{width: "18px"}}/>
            </div>
            <div className="producto-precio">
                <span style={{background: "#eee",padding: "5px",borderRadius: "5px",color: "#444"}}>
                $ {precioTotal}
                </span> 
            </div>
        </article>
    )
}


export default IndexCarrito;